<template>
	<view>
		<!--头部-->
		<view class="flex align-center p-3 bottom-border border-light-secondary">
			<image src="../../static/default.jpg" 
			style="width: 180rpx;height: 180rpx;"
			class="rounded-circle"
			mode=""></image>
			<view class="pl-3 flex flex-column  flex-1">
				<view class="flex align-center">
					<view class="flex-1 flex flex-column align-center justify-center">
						<text class="font-lg font-weight-bold">1</text>
						<text class="font text-muted">粉丝</text>
					</view>
					<view class="flex-1 flex flex-column align-center justify-center">
						<text class="font-lg font-weight-bold">1</text>
						<text class="font text-muted">粉丝</text>
					</view>
					<view class="flex-1 flex flex-column align-center justify-center">
						<text class="font-lg font-weight-bold">1</text>
						<text class="font text-muted">粉丝</text>
					</view>
				</view>
				<view class="flex justify-center align-center">
					<button type="primary" style="width: 400rpx;"  size="mini" class="bg-main w-100">关注</button>
				</view>
			</view>
		</view>
		
		<!--tab-->
		<view class="flex align-center" style="height: 100rpx;">
			<view class="flex-1 flex align-center justify-center" 
			v-for="(item,index) in tabBars" :key="index" :class="index === tabIndex ? 'font-lg font-weight-bold  text-main' : 'font-md'"
			@click="changeTab(index)">
				{{item.name}}
			</view>
		</view>
		
		<template v-if="tabIndex===0">
			<view class="p-3 border-bottom">
				<view class="font-md">
					账号信息
				</view>
				<view class="font">
					账号年龄：12个月
				</view>
				<view class="font">
					账号id：1
				</view>
			</view>
			<view class="p-3 border-bottom">
				<view class="font-md">
					个人信息
				</view>
				<view class="font">
					星座：12个月
				</view>
				<view class="font">
					职业：1
				</view>
				<view class="font">
					故乡：1
				</view>
				<view class="font">
					情感：1
				</view>
			</view>
		</template>
		<template v-else>
			<view class="animated fast fadeIn">
				<common-list v-for="(item,index) in list" :key="index" :item="item" :index="index"  @follow="follow" @doSupport="doSupport"></common-list>
				<divider></divider>
				<load-more :loadmore="loadmore"></load-more>
			</view>
		</template>
		
		<!--弹出层-->
		<uni-popup ref="popup" type="top">
			<view class="w-100" style="background-color: white;">
				<view class="flex align-center justify-center font-md border-bottom py-2" 
				@click="popupEvent('friend')"
				hover-class="bg-light" >
					<text class="iconfont icon-sousuo mr-2"></text> 加入黑名单
				</view>
				<view class="flex align-center justify-center font-md border-bottom py-2"
				 @click="popupEvent('clear')"
				 hover-class="bg-light">
					<text class="iconfont icon-shanchu mr-2"></text> 聊天
				</view>
			</view>
		</uni-popup>
	</view>
</template>

<script>
	const demo = [
		{
			username: '昵称',
			userpic: '../../static/default.jpg',
			newstime: '2019-10-20 下午04:30',
			isFollow: false,
			title: '我是标题',
			titlepic: 'https://tangzhe123-com.oss-cn-shenzhen.aliyuncs.com/Appstatic/qsbk/demo/datapic/4.jpg',
			support: {
				type: 'support',
				support_count: 1,
				unsupport_count: 0
			},
			comment_count: 2,
			share_num: 2,
			content : "uniapp 社区交友",
			images:[{
				url : "https://tangzhe123-com.oss-cn-shenzhen.aliyuncs.com/Appstatic/qsbk/demo/datapic/4.jpg"
			},{
				url : "https://tangzhe123-com.oss-cn-shenzhen.aliyuncs.com/Appstatic/qsbk/demo/datapic/5.jpg"
			}]
		},
		{
			username: '昵称',
			userpic: '../../static/default.jpg',
			newstime: '2019-10-20 下午04:30',
			isFollow: false,
			title: '我是标题',
			titlepic: '../../static/demo/datapic/11.jpg',
			support: {
				type: '',
				support_count: 0,
				unsupport_count: 0
			},
			comment_count: 2,
			share_num: 2
		},
		{
			username: '昵称',
			userpic: '../../static/default.jpg',
			newstime: '2019-10-20 下午04:30',
			isFollow: false,
			title: '我是标题',
			titlepic: '../../static/demo/datapic/11.jpg',
			support: {
				type: '',
				support_count: 0,
				unsupport_count: 0
			},
			comment_count: 2,
			share_num: 2
		}
	];
	
	import commonList from '../../components/common/common_list.vue'
	import loadMore from '@/components/common/load_more.vue';
	import uniPopup from '../../components/uni-ui/uni-popup/uni-popup.vue'
	
	export default {
		components:{
			commonList,
			loadMore,
			uniPopup
		},
		data() {
			return {
				tabIndex : 0,
				tabBars : [{
					name : "主页",
				},{
					name : "帖子",
					list : demo,
					loadmore: '上拉加载更多'
				},{
					name : "动态",
					list : demo,
					loadmore: '上拉加载更多'
				}],
			}
		},
		computed:{
			list(){
				return this.tabBars[this.tabIndex].list
			},
			loadmore(){
				return this.tabBars[this.tabIndex].loadmore
			}
		},
		// 监听原生导航栏
		onNavigationBarButtonTap(){
			this.$refs.popup.open()
		},
		methods: {
			changeTab(index){
				this.tabIndex = index
			},
			follow(index) {
				this.list[index].isFollow = true;
				uni.showToast({
					title: '关注成功'
				});
			},
			doSupport(e) {
				let list = this.tabBars[this.tabIndex].list
				let item = list[e.index]
				let msg = e.type === "support" ? "点赞" : "反对";
				if (item.support.type === '') {
					item.support.type = e.type;
					item.support[e.type + '_count']++;
				}
				if (item.support.type === 'support' && e.type === 'unsupport') {
					item.support.support_count--;
					item.support.unsupport_count++;
				} else if (item.support.type === 'unsupport' && e.type === 'support') {
					item.support.support_count++;
					item.support.unsupport_count--;
				}
				item.support.type = e.type;
				uni.showToast({
					title: msg + "成功"
				})
			},
		}
	}
</script>

